import { ViewportScroller } from "@angular/common";
import { ChangeDetectorRef, Component, OnDestroy, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormControl } from "@angular/forms";
import { NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { Subject, takeUntil } from "rxjs";
import { AdminService } from "src/app/admin/admin.service";
import { UserService } from "src/app/core/user/user.service";
import Swal from "sweetalert2";
import { VerUserAdminComponent } from "../modales/ver-user/ver-user.component";
import { environment } from "src/environments/environment";

@Component({
  selector: "app-user-admin-list",
  templateUrl: "./users-admin-list.component.html",
  styleUrls: ["../user-admin.component.scss"],
})
export class UsersAdminListComponent implements OnInit, OnDestroy {

  @ViewChild("verUserModal") VerUserModal: VerUserAdminComponent;

  private _unsubscribeAll: Subject<any> = new Subject<any>();

  usuariosCount: number = 0;
  usuariosList: any = [];
  page: any = 1;
  limit: any = environment.pagination_limit;
  Toast:any;

  searchInputControl: FormControl = new FormControl('');

  constructor(
    private _userService: UserService,
    private _changeDetectorRef: ChangeDetectorRef,
    private modalService: NgbModal,
    private _formBuilder: FormBuilder,
    private viewScroller: ViewportScroller,
    private _adminService: AdminService
  ) {
    this.Toast = Swal.mixin({
      toast: true,
      position: 'top-end',
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener('mouseenter', Swal.stopTimer)
        toast.addEventListener('mouseleave', Swal.resumeTimer)
      }
    });  
  }

  ngOnInit(): void {
    //Called after the constructor, initializing input properties, and the first call to ngOnChanges.
    //Add 'implements OnInit' to the class.
    this._adminService.usuariosList$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((usuarios: any) => {
        // Update the selected agente
        this.usuariosList = usuarios.users;
        this.usuariosCount = usuarios.totalDocuments;

        // Mark for check
        this._changeDetectorRef.markForCheck();
      });
  }

  /**
  * On destroy
  */
  ngOnDestroy(): void
  {
      // Unsubscribe from all subscriptions
      this._unsubscribeAll.next(null);
      this._unsubscribeAll.complete();
  }

  onPageChange(event:any){
    if(this.searchInputControl.value !== ''){
      this.getUsuariosPaginated(((event - 1) * this.limit), this.searchInputControl.value);
    }else{
      this.getUsuariosPaginated(((event - 1) * this.limit), null);
    }
  }

  getUsuariosPaginated(page:any, name:any){
    this._adminService.getUsuariosListUpdate(page, this.limit, name).pipe(takeUntil(this._unsubscribeAll)).subscribe(
      (response:any) => {
        this._adminService.updateUsuariosList(response);
      },(error) => {
        this.Toast.fire({
          icon: 'error',
          title: error.error.message
        });
      }
    );
  }

  abrirModalVerUsuario(usuario: any){
    this._adminService.updateUsuarioSelected(usuario);
    this.VerUserModal.openModal();
  }

  btnBuscarUsuariosPorNombre(){
    if(this.searchInputControl.value !== ''){
      this.getUsuariosPaginated(0, this.searchInputControl.value);
    }
  }

  btnRefresh(){
    this.searchInputControl.setValue('');
    this.getUsuariosPaginated(0, null);
  }
}
