import { isPlatformBrowser } from "@angular/common";
import { ChangeDetectorRef, Component, Inject, Input, OnDestroy, OnInit, PLATFORM_ID, TemplateRef, ViewChild } from "@angular/core";
import { AbstractControl, FormBuilder, FormGroup, NgForm, ValidationErrors, ValidatorFn, Validators } from "@angular/forms";
import { Router } from "@angular/router";
import { ModalDismissReasons, NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { Gallery, GalleryItem, ImageItem, ImageSize, ThumbnailsPosition } from "ng-gallery";
import { Lightbox } from "ng-gallery/lightbox";
import { Subject, takeUntil } from "rxjs";
import { AdminService } from "src/app/admin/admin.service";
import { UserService } from "src/app/core/user/user.service";
import { environment } from "src/environments/environment";
import Swal from "sweetalert2";

@Component({
  selector: "app-ver-user-admin",
  templateUrl: "./ver-user.component.html",
  styleUrls: ["./ver-user.component.scss"],
})
export class VerUserAdminComponent implements OnInit, OnDestroy {

    @Input() tipo: any;
    @ViewChild("modalUserAdmin", { static: false }) ModalUserAdmin: TemplateRef<any>;
    private _unsubscribeAll: Subject<any> = new Subject<any>();

    public closeResult: string;
    public modalOpen: boolean = false;

    user: any = {};

    Toast:any;

    loading: boolean = true;

    acepting: boolean = true;
    limit: any = environment.pagination_limit;

    items: GalleryItem[];
    constructor(
        @Inject(PLATFORM_ID) private platformId: Object,
        private router: Router, private modalService: NgbModal,
        private _formBuilder: FormBuilder,
        private _userService: UserService,
        private _changeDetectorRef: ChangeDetectorRef,
        private _adminService: AdminService, public gallery: Gallery, public lightbox: Lightbox)
    {
      this.Toast = Swal.mixin({
        toast: true,
        position: 'top-end',
        showConfirmButton: false,
        timer: 3000,
        timerProgressBar: true,
        didOpen: (toast) => {
          toast.addEventListener('mouseenter', Swal.stopTimer)
          toast.addEventListener('mouseleave', Swal.resumeTimer)
        }
      });  
    }

    ngOnInit(): void {
      this._adminService.usuarioSelected$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((user: any) => {
        this.loading = true;
        if(this.tipo === 'solicitudes'){
          this.acepting = true;
        }
        // Update the user list
        if(user !== null){
          this.getUsuarioById(user._id);
        }
        // console.log(this.user);

        // Mark for check
        this._changeDetectorRef.markForCheck();
      });
    }

    ngOnDestroy() {
      if(this.modalOpen){
        this.modalService.dismissAll();
      }
      // Unsubscribe from all subscriptions
      this._unsubscribeAll.next(null);
      this._unsubscribeAll.complete();
    }

    openModal() {
        this.modalOpen = true;
        if (isPlatformBrowser(this.platformId)) { // For SSR 
          this.modalService.open(this.ModalUserAdmin, { 
            size: 'lg',
            ariaLabelledBy: 'modal-basic-title',
            centered: true,
            windowClass: 'ModalUserAdmin',
            backdrop: 'static',
            keyboard: false
          }).result.then((result) => {
            `Result ${result}`
          }, (reason) => {
            this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
          });
        }
    }

    private getDismissReason(reason: any) {
        
    }

    getUsuarioById(id:any){
      this._adminService.getUsuarioById(id).pipe(takeUntil(this._unsubscribeAll)).subscribe(
        (response:any) => {
          this.items = [new ImageItem({ src: response.fotoBase64, thumb: response.fotoBase64 })];
          // this.items = [new ImageItem({ src: this.imagenTemp, thumb: this.imagenTemp })];
          const lightboxRef = this.gallery.ref('lightbox');
          // Add custom gallery config to the lightbox (optional)
          lightboxRef.setConfig({
            imageSize: ImageSize.Cover,
            thumbPosition: ThumbnailsPosition.Top
          });

          // Load items into the lightbox gallery ref
          lightboxRef.load(this.items);
          this.user = response;
          this.loading = false;
        },(error) => {
          this.Toast.fire({
            icon: 'error',
            title: error.error.message
          });
        }
      );
    }

    aceptarUsuarioById(){
      if(this.acepting){
        this.acepting = false;
        this._adminService.aceptarManillaById(this.user._id).pipe(takeUntil(this._unsubscribeAll)).subscribe(
          (response:any) => {
            this.Toast.fire({
              icon: 'success',
              title: 'La manilla ha sido aceptada y puesta en proceso de produccion'
            });
            this.updateUsuariosList();
          },(error) => {
            this.Toast.fire({
              icon: 'error',
              title: error.error.message
            });
          }
        );
      }
    }

    updateUsuariosList(){
      this._adminService.getUsuariosListUpdate(0, this.limit, null).pipe(takeUntil(this._unsubscribeAll)).subscribe(
        (response:any) => {
          this._adminService.updateUsuariosList(response);
          this.modalService.dismissAll();
        },(error) => {
          this.Toast.fire({
            icon: 'error',
            title: error.error.message
          });
        }
      );
    }

    submit(): void{
        
    }
}
